import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Typography,
  Button,
  Chip,
  Card,
  CardContent,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Alert,
  List,
  ListItem,
  ListItemText,
  Divider,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions
} from '@mui/material';
import {
  ArrowBack as ArrowBackIcon,
  Edit as EditIcon,
  Delete as DeleteIcon,
  Add as AddIcon,
  CalendarToday as CalendarIcon,
  CheckCircle as CheckCircleIcon,
  RadioButtonUnchecked as RadioButtonUncheckedIcon
} from '@mui/icons-material';
import { useParams, useNavigate } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';
import tasksService from '../../services/tasks';
import activityLogsService from '../../services/activityLogs';
import projectMessagesService from '../../services/projectMessages';
import dayjs from 'dayjs';

const TaskView = () => {
  const { projectId, taskId } = useParams();
  const navigate = useNavigate();
  const { user } = useAuth();
  const [task, setTask] = useState(null);
  const [activityLogs, setActivityLogs] = useState([]);
  const [taskComments, setTaskComments] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [editMode, setEditMode] = useState(false);
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    dueDate: '',
    priority: 'Medium',
    status: 'Not Started'
  });
  const [newSubtask, setNewSubtask] = useState('');
  const [newComment, setNewComment] = useState('');
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);

  useEffect(() => {
    if (taskId && user?.$id) {
      fetchTask();
      fetchActivityLogs();
      fetchTaskComments();
    }
  }, [taskId, user?.$id]);

  const fetchTaskComments = async () => {
    try {
      const comments = await projectMessagesService.listMessages({ taskId, sortBy: 'newest' });
      setTaskComments(comments);
    } catch (err) {
      console.error('Error fetching task comments:', err);
    }
  };

  const fetchTask = async () => {
    try {
      const taskData = await tasksService.getTask(taskId);
      setTask(taskData);
      setFormData({
        title: taskData.title || '',
        description: taskData.description || '',
        dueDate: taskData.dueDate ? dayjs(taskData.dueDate).format('YYYY-MM-DD') : '',
        priority: taskData.priority || 'Medium',
        status: taskData.status || 'Not Started'
      });
    } catch (err) {
      setError('Failed to load task');
    }
  };

  const fetchActivityLogs = async () => {
    try {
      const logs = await activityLogsService.listActivityLogs({ taskId, sortBy: 'newest' });
      setActivityLogs(logs);
    } catch (err) {
      console.error('Error fetching activity logs:', err);
    }
  };

  const handleSave = async () => {
    setLoading(true);
    try {
      const oldTask = { ...task };
      await tasksService.updateTask(taskId, formData);
      
      // Log changes
      const changes = [];
      if (oldTask.title !== formData.title) {
        changes.push(`Title: ${oldTask.title}  ${formData.title}`);
      }
      if (oldTask.status !== formData.status) {
        changes.push(`Status: ${oldTask.status}  ${formData.status}`);
      }
      if (oldTask.priority !== formData.priority) {
        changes.push(`Priority: ${oldTask.priority}  ${formData.priority}`);
      }

      if (changes.length > 0) {
        await activityLogsService.createActivityLog({
          projectId,
          taskId,
          userId: user.$id,
          userName: user.name || user.email,
          category: 'Task Update',
          action: `Updated task: ${changes.join(', ')}`,
          oldValue: JSON.stringify(oldTask),
          newValue: JSON.stringify(formData)
        });
      }

      setSuccess('Task updated successfully');
      setEditMode(false);
      await fetchTask();
      await fetchActivityLogs();
      setTimeout(() => setSuccess(''), 3000);
    } catch (err) {
      setError(err?.message || 'Failed to update task');
    } finally {
      setLoading(false);
    }
  };

  const handleAddSubtask = async () => {
    if (!newSubtask.trim()) return;

    const subtasks = [...(task.subtasks || []), { id: Date.now(), text: newSubtask.trim(), completed: false }];
    
    setLoading(true);
    try {
      await tasksService.updateTask(taskId, { subtasks });
      
      await activityLogsService.createActivityLog({
        projectId,
        taskId,
        userId: user.$id,
        userName: user.name || user.email,
        category: 'Task Update',
        action: `Added subtask: ${newSubtask}`,
        newValue: newSubtask
      });

      setNewSubtask('');
      await fetchTask();
      await fetchActivityLogs();
    } catch (err) {
      setError(err?.message || 'Failed to add subtask');
    } finally {
      setLoading(false);
    }
  };

  const handleToggleSubtask = async (subtaskId) => {
    const subtasks = task.subtasks.map(st => 
      st.id === subtaskId ? { ...st, completed: !st.completed } : st
    );
    
    setLoading(true);
    try {
      await tasksService.updateTask(taskId, { subtasks });
      await fetchTask();
    } catch (err) {
      setError(err?.message || 'Failed to update subtask');
    } finally {
      setLoading(false);
    }
  };

  const handleAddComment = async () => {
    if (!newComment.trim()) return;
    
    setLoading(true);
    try {
      // Use projectMessages service instead of task comments
      const projectMessagesService = (await import('../../services/projectMessages')).default;
      await projectMessagesService.createMessage({
        projectId,
        taskId,
        userId: user.$id,
        userName: user.name || user.email,
        message: newComment.trim(),
        messageDate: new Date().toISOString()
      });
      
      await activityLogsService.createActivityLog({
        projectId,
        taskId,
        userId: user.$id,
        userName: user.name || user.email,
        category: 'Comment Added',
        action: `Added comment`,
        newValue: newComment
      });

      setNewComment('');
      await fetchTaskComments();
      await fetchActivityLogs();
    } catch (err) {
      setError(err?.message || 'Failed to add comment');
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async () => {
    setLoading(true);
    try {
      await tasksService.deleteTask(taskId);
      
      await activityLogsService.createActivityLog({
        projectId,
        userId: user.$id,
        userName: user.name || user.email,
        category: 'Task Update',
        action: `Deleted task: ${task.title}`,
        oldValue: task.title
      });

      navigate(`/projects/${projectId}`);
    } catch (err) {
      setError(err?.message || 'Failed to delete task');
      setLoading(false);
    }
  };

  const getPriorityColor = (priority) => {
    const colors = {
      'Low': 'success',
      'Medium': 'warning',
      'High': 'error',
      'Critical': 'error'
    };
    return colors[priority] || 'default';
  };

  const isOverdue = (dueDate) => {
    if (!dueDate) return false;
    return new Date(dueDate) < new Date() && new Date(dueDate).toDateString() !== new Date().toDateString();
  };

  if (!task) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Typography>Loading task...</Typography>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Box sx={{ mb: 3, display: 'flex', alignItems: 'center', gap: 2 }}>
        <Button
          startIcon={<ArrowBackIcon />}
          onClick={() => navigate(`/projects/${projectId}`)}
          sx={{ textTransform: 'none' }}
        >
          Back to Project
        </Button>
        <Box sx={{ flexGrow: 1 }} />
        {!editMode && (
          <>
            <Button
              variant="outlined"
              startIcon={<EditIcon />}
              onClick={() => setEditMode(true)}
              sx={{ textTransform: 'none' }}
            >
              Edit
            </Button>
            <Button
              variant="outlined"
              color="error"
              startIcon={<DeleteIcon />}
              onClick={() => setDeleteDialogOpen(true)}
              sx={{ textTransform: 'none' }}
            >
              Delete
            </Button>
          </>
        )}
      </Box>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError('')}>
          {error}
        </Alert>
      )}
      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess('')}>
          {success}
        </Alert>
      )}

      <Card sx={{ mb: 3 }}>
        <CardContent>
          {editMode ? (
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
              <TextField
                fullWidth
                label="Task Title"
                value={formData.title}
                onChange={(e) => setFormData(prev => ({ ...prev, title: e.target.value }))}
                required
              />
              <TextField
                fullWidth
                label="Description"
                value={formData.description}
                onChange={(e) => setFormData(prev => ({ ...prev, description: e.target.value }))}
                multiline
                rows={4}
              />
              <TextField
                fullWidth
                label="Due Date"
                type="date"
                value={formData.dueDate}
                onChange={(e) => setFormData(prev => ({ ...prev, dueDate: e.target.value }))}
                InputLabelProps={{ shrink: true }}
              />
              <Box sx={{ display: 'flex', gap: 2 }}>
                <FormControl fullWidth>
                  <InputLabel>Priority</InputLabel>
                  <Select
                    value={formData.priority}
                    onChange={(e) => setFormData(prev => ({ ...prev, priority: e.target.value }))}
                    label="Priority"
                  >
                    <MenuItem value="Low">Low</MenuItem>
                    <MenuItem value="Medium">Medium</MenuItem>
                    <MenuItem value="High">High</MenuItem>
                    <MenuItem value="Critical">Critical</MenuItem>
                  </Select>
                </FormControl>
                <FormControl fullWidth>
                  <InputLabel>Status</InputLabel>
                  <Select
                    value={formData.status}
                    onChange={(e) => setFormData(prev => ({ ...prev, status: e.target.value }))}
                    label="Status"
                  >
                    <MenuItem value="Not Started">Not Started</MenuItem>
                    <MenuItem value="Active">Active</MenuItem>
                    <MenuItem value="Blocked">Blocked</MenuItem>
                    <MenuItem value="Completed">Completed</MenuItem>
                  </Select>
                </FormControl>
              </Box>
              <Box sx={{ display: 'flex', gap: 1, justifyContent: 'flex-end' }}>
                <Button onClick={() => setEditMode(false)}>Cancel</Button>
                <Button onClick={handleSave} variant="contained" disabled={loading || !formData.title.trim()}>
                  Save
                </Button>
              </Box>
            </Box>
          ) : (
            <>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 2 }}>
                <Typography variant="h4" sx={{ fontWeight: 'bold', flexGrow: 1 }}>
                  {task.title}
                </Typography>
                <Box sx={{ display: 'flex', gap: 1 }}>
                  <Chip label={task.priority} color={getPriorityColor(task.priority)} />
                  <Chip label={task.status} />
                </Box>
              </Box>
              <Typography variant="body1" sx={{ mb: 2, color: 'text.secondary' }}>
                {task.description || 'No description'}
              </Typography>
              {task.dueDate && (
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
                  <CalendarIcon color={isOverdue(task.dueDate) ? 'error' : 'action'} />
                  <Typography
                    variant="body2"
                    color={isOverdue(task.dueDate) ? 'error.main' : 'text.secondary'}
                    sx={{ fontWeight: isOverdue(task.dueDate) ? 'bold' : 'normal' }}
                  >
                    Due: {dayjs(task.dueDate).format('MMMM D, YYYY')}
                    {isOverdue(task.dueDate) && ' (Overdue!)'}
                  </Typography>
                  <Button
                    size="small"
                    onClick={() => navigate(`/calendar?date=${dayjs(task.dueDate).format('YYYY-MM-DD')}`)}
                    sx={{ ml: 2, textTransform: 'none' }}
                  >
                    View in Calendar
                  </Button>
                </Box>
              )}
            </>
          )}
        </CardContent>
      </Card>

      {/* Subtasks */}
      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Typography variant="h6" sx={{ mb: 2 }}>
            Subtasks
          </Typography>
          {task.subtasks && task.subtasks.length > 0 ? (
            <List>
              {task.subtasks.map((subtask) => (
                <ListItem key={subtask.id}>
                  <IconButton
                    onClick={() => handleToggleSubtask(subtask.id)}
                    sx={{ mr: 1 }}
                  >
                    {subtask.completed ? <CheckCircleIcon color="success" /> : <RadioButtonUncheckedIcon />}
                  </IconButton>
                  <ListItemText
                    primary={subtask.text}
                    sx={{
                      textDecoration: subtask.completed ? 'line-through' : 'none',
                      color: subtask.completed ? 'text.secondary' : 'text.primary'
                    }}
                  />
                </ListItem>
              ))}
            </List>
          ) : (
            <Typography variant="body2" color="text.secondary">
              No subtasks yet
            </Typography>
          )}
          <Box sx={{ display: 'flex', gap: 1, mt: 2 }}>
            <TextField
              fullWidth
              size="small"
              placeholder="Add subtask..."
              value={newSubtask}
              onChange={(e) => setNewSubtask(e.target.value)}
              onKeyPress={(e) => {
                if (e.key === 'Enter') {
                  e.preventDefault();
                  handleAddSubtask();
                }
              }}
            />
            <Button variant="outlined" onClick={handleAddSubtask} disabled={!newSubtask.trim()}>
              Add
            </Button>
          </Box>
        </CardContent>
      </Card>

      {/* Comments */}
      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Typography variant="h6" sx={{ mb: 2 }}>
            Comments
          </Typography>
          {taskComments && taskComments.length > 0 ? (
            <List>
              {taskComments.map((comment, index) => (
                <React.Fragment key={comment.$id}>
                  <ListItem>
                    <ListItemText
                      primary={comment.message}
                      secondary={`${comment.userName}  ${dayjs(comment.messageDate).format('MMM D, YYYY')}  ${dayjs(comment.createdAt).format('h:mm A')}`}
                    />
                  </ListItem>
                  {index < taskComments.length - 1 && <Divider />}
                </React.Fragment>
              ))}
            </List>
          ) : (
            <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
              No comments yet
            </Typography>
          )}
          <Box sx={{ display: 'flex', gap: 1 }}>
            <TextField
              fullWidth
              multiline
              rows={2}
              placeholder="Add a comment..."
              value={newComment}
              onChange={(e) => setNewComment(e.target.value)}
            />
            <Button variant="outlined" onClick={handleAddComment} disabled={!newComment.trim()}>
              Add
            </Button>
          </Box>
        </CardContent>
      </Card>

      {/* Activity Log */}
      <Card>
        <CardContent>
          <Typography variant="h6" sx={{ mb: 2 }}>
            Activity History
          </Typography>
          <List>
            {activityLogs.length === 0 ? (
              <ListItem>
                <ListItemText primary="No activity yet" />
              </ListItem>
            ) : (
              activityLogs.map((log, index) => (
                <React.Fragment key={log.$id}>
                  <ListItem>
                    <ListItemText
                      primary={log.action}
                      secondary={
                        <Box>
                          <Typography variant="caption" color="text.secondary">
                            {log.userName}  {dayjs(log.timestamp).format('MMM D, YYYY h:mm A')}
                          </Typography>
                          {log.oldValue && log.newValue && (
                            <Typography variant="caption" color="text.secondary" sx={{ display: 'block' }}>
                              {log.oldValue}  {log.newValue}
                            </Typography>
                          )}
                        </Box>
                      }
                    />
                    <Chip label={log.category} size="small" />
                  </ListItem>
                  {index < activityLogs.length - 1 && <Divider />}
                </React.Fragment>
              ))
            )}
          </List>
        </CardContent>
      </Card>

      {/* Delete Confirmation Dialog */}
      <Dialog open={deleteDialogOpen} onClose={() => setDeleteDialogOpen(false)}>
        <DialogTitle>Delete Task</DialogTitle>
        <DialogContent>
          <Typography>
            Are you sure you want to delete "{task.title}"? This cannot be undone.
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDeleteDialogOpen(false)}>Cancel</Button>
          <Button onClick={handleDelete} color="error" variant="contained" disabled={loading}>
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default TaskView;

