import { databases } from '../appwrite';
import { Query, ID } from 'appwrite';

const DATABASE_ID = process.env.REACT_APP_APPWRITE_DATABASE_ID || '69181c670024df0dd32b';
// Use numeric ID '6918f632002d42536ab2' where the profile actually exists
// Fallback to 'profiles' name if env var not set (Appwrite accepts both)
const PROFILES_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_PROFILES || '6918f632002d42536ab2';

export async function createProfile(userId, name, email, role = 'user') {
  // Collection-level permissions handle access control
  return databases.createDocument(
    DATABASE_ID,
    PROFILES_COLLECTION_ID,
    ID.unique(),
    { userId, name, email, role }
  );
}

export async function getProfileByUserId(userId) {
  try {
    console.log('[Profiles] Fetching profile for userId:', userId);
    console.log('[Profiles] Using collection:', PROFILES_COLLECTION_ID);
    console.log('[Profiles] Database ID:', DATABASE_ID);
    console.log('[Profiles] Env var REACT_APP_APPWRITE_COLLECTION_PROFILES:', process.env.REACT_APP_APPWRITE_COLLECTION_PROFILES);
    
    const res = await databases.listDocuments(DATABASE_ID, PROFILES_COLLECTION_ID, [
      Query.equal('userId', userId)
    ]);
    
    console.log('[Profiles] Query result:', res?.documents?.length || 0, 'documents');
    
    if (!res || !res.documents || res.documents.length === 0) {
      console.warn('[Profiles] No profile found for userId:', userId);
      console.warn('[Profiles] Tried collection:', PROFILES_COLLECTION_ID);
      
      // Try alternative collection IDs as fallback
      const alternativeIds = ['profiles', '6918f632002d42536ab2'];
      for (const altId of alternativeIds) {
        if (altId === PROFILES_COLLECTION_ID) continue;
        try {
          console.log('[Profiles] Trying alternative collection:', altId);
          const altRes = await databases.listDocuments(DATABASE_ID, altId, [
            Query.equal('userId', userId)
          ]);
          if (altRes?.documents?.length > 0) {
            console.log('[Profiles] Found profile in alternative collection:', altId);
            return altRes.documents[0];
          }
        } catch (e) {
          console.warn('[Profiles] Alternative collection failed:', altId, e.message);
        }
      }
      
      return null;
    }
    
    const profile = res.documents[0];
    console.log('[Profiles]  Found profile:', { 
      role: profile.role, 
      userId: profile.userId,
      email: profile.email,
      documentId: profile.$id
    });
    return profile;
  } catch (error) {
    console.error('[Profiles] Error fetching profile:', error.message || error);
    console.error('[Profiles] Error details:', { 
      code: error.code, 
      type: error.type,
      collection: PROFILES_COLLECTION_ID,
      database: DATABASE_ID
    });
    // Don't throw - return null so app can continue
    return null;
  }
}

export async function listProfiles() {
  try {
    console.log('[Profiles] listProfiles - Fetching all profiles');
    console.log('[Profiles] Using collection:', PROFILES_COLLECTION_ID);
    console.log('[Profiles] Database ID:', DATABASE_ID);
    const res = await databases.listDocuments(DATABASE_ID, PROFILES_COLLECTION_ID);
    const documents = res.documents || [];
    console.log(`[Profiles] listProfiles - Found ${documents.length} profiles:`, documents.map(d => ({ email: d.email, name: d.name, role: d.role })));
    return documents;
  } catch (error) {
    console.error('[Profiles] Error listing profiles:', error.message || error);
    throw error;
  }
}

export async function updateProfileRole(documentId, role) {
  return databases.updateDocument(DATABASE_ID, PROFILES_COLLECTION_ID, documentId, { role });
}

export default {
  createProfile,
  getProfileByUserId,
  listProfiles,
  updateProfileRole,
};
