import React, { useState, useEffect, useCallback } from 'react';
import {
  Container,
  Box,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Alert,
  Tabs,
  Tab,
  IconButton,
  Chip,
  Switch,
  FormControlLabel,
  Snackbar,
  AppBar,
  Toolbar
} from '@mui/material';
import {
  Delete as DeleteIcon,
  Edit as EditIcon,
  Key as KeyIcon,
  Visibility as VisibilityIcon,
  VisibilityOff as VisibilityOffIcon,
  Lock as LockIcon,
  Refresh as RefreshIcon,
  ArrowBack as ArrowBackIcon,
  Add as AddIcon
} from '@mui/icons-material';
import { useAuth } from '../../contexts/AuthContext';
import profilesService from '../../services/profiles';
import passwordsService from '../../services/passwords';
import groupsService from '../../services/groups';
import logsService from '../../services/logs';

const AdminPortal = () => {
  const { user } = useAuth();
  const [activeTab, setActiveTab] = useState(0);
  const [users, setUsers] = useState([]);
  const [passwordEntries, setPasswordEntries] = useState([]);
  const [groups, setGroups] = useState([]);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [selectedUser, setSelectedUser] = useState(null);
  const [selectedEntry, setSelectedEntry] = useState(null);
  const [newPassword, setNewPassword] = useState('');
  const [showPassword, setShowPassword] = useState({});
  const [decryptedPasswords, setDecryptedPasswords] = useState({});
  const [dialogOpen, setDialogOpen] = useState(false);
  const [dialogType, setDialogType] = useState('');
  const [editUserData, setEditUserData] = useState({
    name: '',
    email: '',
    isAdmin: false
  });
  const [newUserData, setNewUserData] = useState({
    name: '',
    email: '',
    password: '',
    role: 'user'
  });
  const [loginLogs, setLoginLogs] = useState([]);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });

  const fetchData = useCallback(async () => {
    try {
      switch (activeTab) {
        case 0:
          await fetchUsers();
          break;
        case 1:
          await fetchPasswordEntries();
          break;
        case 2:
          await fetchGroups();
          break;
        case 3:
          await fetchLoginLogs();
          break;
        default:
          console.log('Unknown tab selected');
          break;
      }
    } catch (err) {
      setError('Failed to fetch data');
    }
  }, [activeTab]);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  const fetchUsers = async () => {
    try {
      const docs = await profilesService.listProfiles();
      const mapped = (Array.isArray(docs) ? docs : (docs.documents || [])).map(doc => ({
        _id: doc.$id,
        userId: doc.userId,
        name: doc.name || '',
        email: doc.email || '',
        role: doc.role || 'user',
        hasProfile: true
      }));
      setUsers(mapped);
      if (mapped.length === 0) {
        console.warn('[AdminPortal] No users found in profiles collection');
        console.warn('[AdminPortal] Some users may exist in Appwrite but not have profiles');
        console.warn('[AdminPortal] Run: node scripts/syncUsersToProfiles.js to sync all users');
      } else {
        console.log(`[AdminPortal] Loaded ${mapped.length} users with profiles`);
      }
    } catch (error) {
      console.error('Error fetching users:', error);
      showSnackbar('Error fetching users: ' + (error.message || 'Unknown error'), 'error');
    }
  };

  const fetchPasswordEntries = async () => {
    try {
      // Pass null to list all passwords (admin access)
      const res = await passwordsService.listPasswords(null);
      const mapped = (Array.isArray(res) ? res : (res.documents || [])).map(d => ({
        _id: d.$id || d._id,
        title: d.title,
        username: d.username,
        password_encrypted: d.encryptedPassword || d.password_encrypted,
        owner: d.owner || (d.ownerId ? { name: d.ownerId, email: '' } : null),
        group: d.group || d.groupId || null,
        ...d
      }));
      setPasswordEntries(mapped);
    } catch (error) {
      console.error('Error fetching password entries:', error);
      showSnackbar('Error fetching password entries: ' + (error.message || 'Unknown error'), 'error');
    }
  };

  const fetchGroups = async () => {
    try {
      const res = await groupsService.listGroups();
      const mapped = (Array.isArray(res) ? res : (res.documents || [])).map(d => ({
        _id: d.$id || d._id,
        name: d.name,
        owner: d.owner || d.ownerId || null,
        members: d.members || []
      }));
      setGroups(mapped);
    } catch (error) {
      console.error('Error fetching groups:', error);
      showSnackbar('Error fetching groups: ' + (error.message || 'Unknown error'), 'error');
    }
  };

  const fetchLoginLogs = async () => {
    try {
      const res = await logsService.listLogs();
      const mapped = (Array.isArray(res) ? res : (res.documents || [])).map(d => ({
        _id: d.$id || d._id,
        userName: d.userName || d.name || '',
        userEmail: d.userEmail || d.email || '',
        timestamp: d.timestamp || d.$createdAt,
        ipAddress: d.ipAddress || 'N/A',
        success: d.success !== undefined ? d.success : true
      }));
      setLoginLogs(mapped);
      if (mapped.length === 0) {
        console.log('[AdminPortal] No login logs found (collection may not exist)');
      }
    } catch (error) {
      console.error('Error fetching login logs:', error);
      // Don't show error if collection doesn't exist - it's optional
      if (!error.message?.includes('not be found') && error.code !== 404) {
        showSnackbar('Error fetching login logs: ' + (error.message || 'Unknown error'), 'error');
      } else {
        setLoginLogs([]); // Set empty array if collection doesn't exist
      }
    }
  };

  const handleCreateUser = () => {
    setNewUserData({ name: '', email: '', password: '', role: 'user' });
    setDialogType('createUser');
    setDialogOpen(true);
  };

  const handleCreateUserSubmit = async () => {
    try {
      if (!newUserData.name || !newUserData.email || !newUserData.password) {
        setError('All fields are required');
        return;
      }
      if (newUserData.password.length < 6) {
        setError('Password must be at least 6 characters long');
        return;
      }

      // User creation requires server-side API key
      // For now, show instructions
      const instructions = `To create user "${newUserData.email}":\n\n` +
        `Run this command:\n` +
        `node scripts/createUser.js "${newUserData.name}" "${newUserData.email}" "${newUserData.password}" "${newUserData.role}"\n\n` +
        `Make sure APPWRITE_API_KEY is set in your environment.`;
      
      alert(instructions);
      showSnackbar('See alert for user creation instructions', 'info');
      setDialogOpen(false);
      setNewUserData({ name: '', email: '', password: '', role: 'user' });
      
      // Refresh user list after a moment (in case user runs the script)
      setTimeout(() => {
        fetchUsers();
      }, 2000);
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to create user');
    }
  };

  const handleEditUser = (user) => {
    setSelectedUser(user);
    setEditUserData({
      name: user.name,
      email: user.email,
      isAdmin: user.role === 'admin'
    });
    setDialogType('editUser');
    setDialogOpen(true);
  };

  const handleUpdateUser = async () => {
    try {
      if (selectedUser._id) {
        const newRole = editUserData.isAdmin ? 'admin' : 'user';
        await profilesService.updateProfileRole(selectedUser._id, newRole);
        setSuccess('User role updated successfully');
      }
      setDialogOpen(false);
      fetchUsers();
    } catch (err) {
      setError('Failed to update user');
    }
  };

  const handleDeleteUser = async (userId) => {
    if (!window.confirm('Are you sure? This will delete all user data including passwords and groups.')) {
      return;
    }

    try {
      showSnackbar('To delete a user, use the Appwrite console or run a server-side admin script.', 'info');
    } catch (err) {
      setError('Failed to initiate delete');
    }
  };

  const handleChangePassword = async () => {
    try {
      if (!newPassword || newPassword.length < 6) {
        setError('Password must be at least 6 characters long');
        return;
      }
      
      // Admins can reset any password, including other admins
      // Changing a user's account password requires server-side privileges with Appwrite API key.
      const userRole = selectedUser?.role || 'user';
      const isAdminUser = userRole === 'admin';
      const instructions = `To reset password for ${selectedUser?.email}${isAdminUser ? ' (Admin)' : ''}:\n\n` +
        `Run this command:\n` +
        `node scripts/resetUserPassword.js "${selectedUser?.email}" "${newPassword}"\n\n` +
        `Note: As an admin, you can reset any user's password, including other admins.\n` +
        `Make sure APPWRITE_API_KEY is set in your environment.`;
      
      alert(instructions);
      showSnackbar('See alert for password reset instructions', 'info');
      setDialogOpen(false);
      setNewPassword('');
      setDialogType('');
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to change password');
    }
  };

  const handlePasswordChange = (user) => {
    setSelectedUser(user);
    setDialogType('changePassword');
    setDialogOpen(true);
  };

  const handleDeletePasswordEntry = async (entryId) => {
    if (!window.confirm('Are you sure you want to delete this password entry?')) {
      return;
    }

    try {
      await passwordsService.deletePassword(entryId);
      setSuccess('Password entry deleted successfully');
      fetchPasswordEntries();
    } catch (err) {
      setError('Failed to delete password entry');
    }
  };

  const handleDeleteGroup = async (groupId) => {
    if (!window.confirm('Are you sure you want to delete this group?')) {
      return;
    }

    try {
      await groupsService.deleteGroup(groupId);
      setSuccess('Group deleted successfully');
      fetchGroups();
    } catch (err) {
      setError('Failed to delete group');
    }
  };

  const handleCloseDialog = () => {
    setDialogOpen(false);
    setNewPassword('');
    setSelectedUser(null);
    setSelectedEntry(null);
    setEditUserData({ name: '', email: '', isAdmin: false });
    setNewUserData({ name: '', email: '', password: '', role: 'user' });
  };

  const showSnackbar = (message, severity = 'success') => {
    setSnackbar({ open: true, message, severity });
  };

  const handleSnackbarClose = () => {
    setSnackbar({ ...snackbar, open: false });
  };

  const handleBack = () => {
    window.history.back();
  };

  const role = user?.profile?.role || 'user';
  if (role !== 'admin' && role !== 'developer') {
    return (
      <Container>
        <Typography variant="h4" sx={{ mt: 4, mb: 2 }}>
          Access Denied
        </Typography>
        <Typography>
          You do not have permission to access the admin portal.
        </Typography>
      </Container>
    );
  }

  return (
    <>
      <AppBar position="static" color="transparent" elevation={0}>
        <Toolbar>
          <IconButton
            edge="start"
            color="inherit"
            onClick={handleBack}
            sx={{ mr: 2 }}
          >
            <ArrowBackIcon />
          </IconButton>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            Admin Portal
          </Typography>
        </Toolbar>
      </AppBar>
      
      <Container maxWidth="xl">
        <Box sx={{ 
          display: 'flex', 
          flexDirection: 'column',
          gap: 2,
          py: 4
        }}>
          <Typography 
            variant="h4" 
            sx={{ 
              fontWeight: 'medium',
              color: 'primary.main',
              mb: 1
            }}
          >
            Admin Portal
          </Typography>

          {error && (
            <Alert 
              severity="error" 
              variant="outlined"
              sx={{ 
                '& .MuiAlert-message': { 
                  display: 'flex', 
                  alignItems: 'center' 
                } 
              }}
            >
              {error}
            </Alert>
          )}
          
          {success && (
            <Alert 
              severity="success"
              variant="outlined"
              sx={{ 
                '& .MuiAlert-message': { 
                  display: 'flex', 
                  alignItems: 'center' 
                } 
              }}
            >
              {success}
            </Alert>
          )}

          <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 2 }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
              <Tabs 
                value={activeTab} 
                onChange={(e, newValue) => setActiveTab(newValue)}
                sx={{ 
                  '& .MuiTab-root': {
                    textTransform: 'none',
                    fontSize: '0.95rem',
                    minHeight: 48,
                    py: 0
                  }
                }}
              >
                <Tab label="Users" />
                <Tab label="Password Entries" />
                <Tab label="Groups" />
                <Tab label="Login Logs" />
              </Tabs>
              {activeTab === 0 && (
                <Button
                  variant="contained"
                  color="primary"
                  startIcon={<AddIcon />}
                  onClick={handleCreateUser}
                  sx={{ ml: 2 }}
                >
                  Create User
                </Button>
              )}
            </Box>
          </Box>

          <TableContainer 
            component={Paper}
            sx={{ 
              boxShadow: 2,
              borderRadius: 1,
              overflow: 'hidden'
            }}
          >
            <Table size="small">
              {activeTab === 0 && (
                <>
                  <TableHead>
                    <TableRow>
                      <TableCell sx={{ fontWeight: 'bold' }}>Name</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Email</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Role</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {users.length === 0 ? (
                      <TableRow>
                        <TableCell colSpan={4} align="center" sx={{ py: 4 }}>
                          <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                            No users found in profiles collection
                          </Typography>
                          <Typography variant="caption" color="text.secondary" sx={{ display: 'block', mb: 1 }}>
                            Some users may exist in Appwrite but don't have profiles yet.
                          </Typography>
                          <Typography variant="caption" color="text.secondary" sx={{ display: 'block', fontFamily: 'monospace', mb: 2 }}>
                            Run: node scripts/syncUsersToProfiles.js
                          </Typography>
                          <Button
                            variant="outlined"
                            size="small"
                            onClick={() => {
                              const instructions = `To sync all Appwrite users to profiles:\n\n` +
                                `Run this command:\n` +
                                `node scripts/syncUsersToProfiles.js\n\n` +
                                `Make sure APPWRITE_API_KEY is set in your environment.`;
                              alert(instructions);
                            }}
                          >
                            Show Sync Instructions
                          </Button>
                        </TableCell>
                      </TableRow>
                    ) : (
                      users.map((user) => (
                        <TableRow key={user._id || user.userId}>
                          <TableCell sx={{ minWidth: 120 }}>{user.name || 'N/A'}</TableCell>
                          <TableCell sx={{ minWidth: 180 }}>{user.email || 'N/A'}</TableCell>
                          <TableCell sx={{ minWidth: 100 }}>
                            <Chip 
                              label={user.role ? user.role.charAt(0).toUpperCase() + user.role.slice(1) : 'User'}
                              color={user.role === 'developer' ? 'secondary' : (user.role === 'admin' ? 'primary' : 'default')}
                              size="small"
                              sx={{ fontSize: '0.75rem', height: '24px' }}
                            />
                          </TableCell>
                          <TableCell>
                            <IconButton
                              size="small"
                              onClick={() => handlePasswordChange(user)}
                              color="primary"
                              title={`Reset password for ${user.email}${user.role === 'admin' ? ' (Admin)' : ''}. Admins can reset any password, including other admins.`}
                            >
                              <KeyIcon />
                            </IconButton>
                            <IconButton
                              size="small"
                              onClick={() => handleEditUser(user)}
                              color="primary"
                              title="Edit user"
                            >
                              <EditIcon />
                            </IconButton>
                            <IconButton
                              size="small"
                              onClick={() => handleDeleteUser(user._id)}
                              color="error"
                              title="Delete user"
                            >
                              <DeleteIcon />
                            </IconButton>
                          </TableCell>
                        </TableRow>
                      ))
                    )}
                  </TableBody>
                </>
              )}

              {activeTab === 1 && (
                <>
                  <TableHead>
                    <TableRow>
                      <TableCell sx={{ fontWeight: 'bold' }}>Title</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Username</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Password</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Owner</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Group</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {passwordEntries.map((entry) => (
                      <TableRow key={entry._id}>
                        <TableCell sx={{ minWidth: 150 }}>{entry.title}</TableCell>
                        <TableCell sx={{ minWidth: 120 }}>{entry.username}</TableCell>
                        <TableCell sx={{ minWidth: 150 }}>
                          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                            <Typography sx={{ fontFamily: 'monospace' }}>
                              {showPassword[entry._id] ? (decryptedPasswords[entry._id] || 'Decrypting...') : ''}
                            </Typography>
                            <IconButton
                              size="small"
                              onClick={async () => {
                                const currently = !!showPassword[entry._id];
                                if (!currently) {
                                  // showing now: decrypt first if needed
                                  if (!decryptedPasswords[entry._id] && entry.password_encrypted) {
                                    try {
                                      const plain = await passwordsService.getDecryptedPassword(entry.password_encrypted);
                                      setDecryptedPasswords(prev => ({ ...prev, [entry._id]: plain }));
                                    } catch (e) {
                                      setDecryptedPasswords(prev => ({ ...prev, [entry._id]: 'Error' }));
                                    }
                                  }
                                }
                                setShowPassword(prev => ({ ...prev, [entry._id]: !prev[entry._id] }));
                              }}
                            >
                              {showPassword[entry._id] ? <VisibilityOffIcon fontSize="small" /> : <VisibilityIcon fontSize="small" />}
                            </IconButton>
                          </Box>
                        </TableCell>
                        <TableCell sx={{ minWidth: 120 }}>
                          {entry.owner?.name || (typeof entry.owner === 'string' ? entry.owner : 'Unknown')}
                        </TableCell>
                        <TableCell sx={{ minWidth: 120 }}>{entry.group?.name || '-'}</TableCell>
                        <TableCell>
                          <IconButton 
                            onClick={() => handleDeletePasswordEntry(entry._id)}
                            size="small"
                            color="error"
                          >
                            <DeleteIcon fontSize="small" />
                          </IconButton>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </>
              )}

              {activeTab === 2 && (
                <>
                  <TableHead>
                    <TableRow>
                      <TableCell sx={{ fontWeight: 'bold' }}>Name</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Owner</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Members</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {groups.map((group) => (
                      <TableRow key={group._id}>
                        <TableCell sx={{ minWidth: 150 }}>{group.name}</TableCell>
                        <TableCell sx={{ minWidth: 120 }}>{group.owner?.name}</TableCell>
                        <TableCell sx={{ minWidth: 200 }}>
                          <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                            {group.members.map(member => (
                              <Chip
                                key={member.user._id}
                                label={`${member.user.name} (${member.role})`}
                                size="small"
                                color={member.role === 'admin' ? 'primary' : 'default'}
                                sx={{ 
                                  fontSize: '0.75rem',
                                  height: '24px'
                                }}
                              />
                            ))}
                          </Box>
                        </TableCell>
                        <TableCell>
                          <IconButton 
                            onClick={() => handleDeleteGroup(group._id)}
                            size="small"
                            color="error"
                          >
                            <DeleteIcon fontSize="small" />
                          </IconButton>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </>
              )}

              {activeTab === 3 && (
                <>
                  <TableHead>
                    <TableRow>
                      <TableCell sx={{ fontWeight: 'bold' }}>User</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Email</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Timestamp</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>IP Address</TableCell>
                      <TableCell sx={{ fontWeight: 'bold' }}>Status</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {loginLogs.map((log) => (
                      <TableRow key={log._id}>
                        <TableCell sx={{ minWidth: 120 }}>{log.userName}</TableCell>
                        <TableCell sx={{ minWidth: 180 }}>{log.userEmail}</TableCell>
                        <TableCell sx={{ minWidth: 160, fontFamily: 'monospace' }}>
                          {new Date(log.timestamp).toLocaleString()}
                        </TableCell>
                        <TableCell sx={{ minWidth: 120, fontFamily: 'monospace' }}>{log.ipAddress}</TableCell>
                        <TableCell sx={{ minWidth: 100 }}>
                          <Chip
                            label={log.success ? 'Success' : 'Failed'}
                            color={log.success ? 'success' : 'error'}
                            size="small"
                            sx={{ 
                              fontSize: '0.75rem',
                              height: '24px',
                              minWidth: '80px'
                            }}
                          />
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </>
              )}
            </Table>
          </TableContainer>
        </Box>

        <Dialog 
          open={dialogOpen && dialogType === 'changePassword'} 
          onClose={handleCloseDialog}
          maxWidth="sm"
          fullWidth
        >
          <DialogTitle>
            Change Password for {selectedUser?.name}
            {selectedUser?.role === 'admin' && (
              <Chip 
                label="Admin" 
                color="primary" 
                size="small" 
                sx={{ ml: 1, fontSize: '0.7rem', height: '20px' }}
              />
            )}
          </DialogTitle>
          <DialogContent>
            <Box sx={{ pt: 2 }}>
              {selectedUser?.role === 'admin' && (
                <Box sx={{ mb: 2, p: 1.5, bgcolor: 'info.light', borderRadius: 1, border: '1px solid', borderColor: 'info.main' }}>
                  <Typography variant="body2" sx={{ fontWeight: 'medium', mb: 0.5 }}>
                    Admin Password Reset
                  </Typography>
                  <Typography variant="caption" color="text.secondary">
                    As an admin, you can reset any user's password, including other admins. Only admins have this privilege.
                  </Typography>
                </Box>
              )}
              <TextField
                fullWidth
                type="password"
                label="New Password"
                value={newPassword}
                onChange={(e) => setNewPassword(e.target.value)}
                margin="dense"
                autoFocus
                error={!!error && error.includes('Password')}
                helperText={error && error.includes('Password') ? error : 'Minimum 6 characters'}
              />
            </Box>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleCloseDialog}>Cancel</Button>
            <Button 
              onClick={handleChangePassword} 
              variant="contained"
              disabled={!newPassword || newPassword.length < 6}
            >
              Change Password
            </Button>
          </DialogActions>
        </Dialog>

        <Dialog 
          open={dialogOpen} 
          onClose={handleCloseDialog}
          maxWidth="sm"
          fullWidth
          PaperProps={{
            sx: {
              borderRadius: 1,
              boxShadow: 3
            }
          }}
        >
          <DialogTitle sx={{ pb: 1 }}>
            {dialogType === 'createUser' ? 'Create New User' : 
             dialogType === 'editUser' ? 'Edit User' : 'Change Password'}
          </DialogTitle>
          <DialogContent sx={{ pb: 2 }}>
            {dialogType === 'createUser' ? (
              <Box sx={{ pt: 2 }}>
                <TextField
                  fullWidth
                  label="Full Name"
                  value={newUserData.name}
                  onChange={(e) => setNewUserData(prev => ({ ...prev, name: e.target.value }))}
                  sx={{ mb: 2 }}
                  size="small"
                  required
                  autoFocus
                />
                <TextField
                  fullWidth
                  label="Email"
                  type="email"
                  value={newUserData.email}
                  onChange={(e) => setNewUserData(prev => ({ ...prev, email: e.target.value }))}
                  sx={{ mb: 2 }}
                  size="small"
                  required
                />
                <TextField
                  fullWidth
                  label="Password"
                  type="password"
                  value={newUserData.password}
                  onChange={(e) => setNewUserData(prev => ({ ...prev, password: e.target.value }))}
                  sx={{ mb: 2 }}
                  size="small"
                  required
                  helperText="Minimum 6 characters"
                />
                <FormControlLabel
                  control={
                    <Switch
                      checked={newUserData.role === 'admin'}
                      onChange={(e) => setNewUserData(prev => ({ 
                        ...prev, 
                        role: e.target.checked ? 'admin' : 'user' 
                      }))}
                      color="primary"
                    />
                  }
                  label={
                    <Typography variant="body2">
                      Admin Access
                    </Typography>
                  }
                />
              </Box>
            ) : dialogType === 'editUser' ? (
              <Box sx={{ pt: 2 }}>
                <TextField
                  fullWidth
                  label="Name"
                  value={editUserData.name}
                  onChange={(e) => setEditUserData(prev => ({ ...prev, name: e.target.value }))}
                  sx={{ mb: 2 }}
                  size="small"
                />
                <TextField
                  fullWidth
                  label="Email"
                  value={editUserData.email}
                  onChange={(e) => setEditUserData(prev => ({ ...prev, email: e.target.value }))}
                  sx={{ mb: 2 }}
                  size="small"
                />
                <FormControlLabel
                  control={
                    <Switch
                      checked={editUserData.isAdmin}
                      onChange={(e) => setEditUserData(prev => ({ ...prev, isAdmin: e.target.checked }))}
                      color="primary"
                    />
                  }
                  label={
                    <Typography variant="body2">
                      Admin Access
                    </Typography>
                  }
                />
              </Box>
            ) : (
              <TextField
                autoFocus
                margin="dense"
                label="New Password"
                type="password"
                fullWidth
                value={newPassword}
                onChange={(e) => setNewPassword(e.target.value)}
                size="small"
                sx={{ mt: 1 }}
              />
            )}
          </DialogContent>
          <DialogActions sx={{ px: 3, pb: 2 }}>
            <Button 
              onClick={handleCloseDialog}
              size="small"
            >
              Cancel
            </Button>
            <Button
              onClick={dialogType === 'createUser' ? handleCreateUserSubmit : 
                      dialogType === 'editUser' ? handleUpdateUser : handleChangePassword}
              variant="contained"
              size="small"
              disabled={dialogType === 'createUser' && (!newUserData.name || !newUserData.email || !newUserData.password || newUserData.password.length < 6)}
            >
              {dialogType === 'createUser' ? 'Create User' :
               dialogType === 'editUser' ? 'Update User' : 'Change Password'}
            </Button>
          </DialogActions>
        </Dialog>

        <Snackbar
          open={snackbar.open}
          autoHideDuration={4000}
          onClose={handleSnackbarClose}
          anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
        >
          <Alert
            onClose={handleSnackbarClose}
            severity={snackbar.severity}
            variant="filled"
            sx={{ 
              width: '100%',
              boxShadow: 2
            }}
          >
            {snackbar.message}
          </Alert>
        </Snackbar>
      </Container>
    </>
  );
};

export default AdminPortal; 