import { databases } from '../appwrite';

const DATABASE_ID = process.env.REACT_APP_APPWRITE_DATABASE_ID || '69181c670024df0dd32b';
const LOGS_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_LOGS || 'logs';

export async function listLogs() {
  try {
    const res = await databases.listDocuments(DATABASE_ID, LOGS_COLLECTION_ID);
    return res.documents || [];
  } catch (error) {
    // If collection doesn't exist, return empty array instead of throwing
    if (error.code === 404 || error.message?.includes('not be found')) {
      console.warn('[Logs] Collection not found, returning empty array');
      return [];
    }
    console.error('[Logs] Error fetching logs:', error.message || error);
    return [];
  }
}

export default { listLogs };
