import React, { useState } from 'react';
import {
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  Typography,
  Box,
  Chip,
  Alert
} from '@mui/material';
import {
  Delete as DeleteIcon,
  PersonAdd as PersonAddIcon,
  PersonRemove as PersonRemoveIcon
} from '@mui/icons-material';
import groupsService from '../../services/groups';

const NoteGroupList = ({ groups, onUpdate }) => {
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [selectedGroup, setSelectedGroup] = useState(null);
  const [addMemberDialogOpen, setAddMemberDialogOpen] = useState(false);
  const [newMemberEmail, setNewMemberEmail] = useState('');
  const [newMemberNickname, setNewMemberNickname] = useState('');
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  const handleDeleteClick = (group) => {
    setSelectedGroup(group);
    setDeleteDialogOpen(true);
  };

  const handleDelete = async () => {
    try {
      await groupsService.deleteGroup(selectedGroup._id);
      onUpdate();
      setDeleteDialogOpen(false);
      setSuccess('Group deleted successfully');
      setTimeout(() => setSuccess(''), 3000);
    } catch (err) {
      console.error('Delete error:', err);
      setError('Failed to delete group');
      setTimeout(() => setError(''), 3000);
    }
  };

  const handleAddMemberClick = (group) => {
    setSelectedGroup(group);
    setAddMemberDialogOpen(true);
  };

  const handleAddMember = async () => {
    if (!newMemberEmail.trim()) {
      setError('Please enter an email address');
      setTimeout(() => setError(''), 3000);
      return;
    }
    
    try {
      await groupsService.addMemberToGroup(
        selectedGroup.$id || selectedGroup._id, 
        newMemberEmail.trim(),
        newMemberNickname.trim()
      );
      setAddMemberDialogOpen(false);
      setNewMemberEmail('');
      setNewMemberNickname('');
      setSuccess('Member added successfully');
      setTimeout(() => setSuccess(''), 3000);
      onUpdate(); // Refresh the groups list
    } catch (err) {
      console.error('Add member error:', err);
      setError(err?.message || 'Failed to add member');
      setTimeout(() => setError(''), 5000);
    }
  };

  const handleRemoveMember = async (groupId, userId) => {
    try {
      await groupsService.removeMemberFromGroup(groupId, userId);
      setSuccess('Member removed successfully');
      setTimeout(() => setSuccess(''), 3000);
      onUpdate(); // Refresh the groups list
    } catch (err) {
      console.error('Remove member error:', err);
      setError(err?.message || 'Failed to remove member');
      setTimeout(() => setError(''), 5000);
    }
  };

  return (
    <Box>
      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}
      {success && (
        <Alert severity="success" sx={{ mb: 2 }}>
          {success}
        </Alert>
      )}
      <List>
        {Array.isArray(groups) && groups.map((group) => (
          <ListItem key={group._id}>
            <ListItemText
              primary={group.name}
              secondaryTypographyProps={{ component: 'div' }}
              secondary={
                <Box component="div">
                  <Typography component="div" variant="body2" color="text.secondary">
                    {group.description}
                  </Typography>
                  <Box component="div" sx={{ mt: 1 }}>
                    {group.membersWithInfo && Array.isArray(group.membersWithInfo) && group.membersWithInfo.length > 0 ? (
                      group.membersWithInfo.map((member, index) => {
                        const displayName = member.nickname || member.name || member.email || 'Unknown';
                        return (
                          <Chip
                            key={member.userId || index}
                            label={displayName}
                            size="small"
                            color="default"
                            sx={{ mr: 0.5, mb: 0.5 }}
                            onDelete={() => handleRemoveMember(group.$id || group._id, member.userId)}
                            deleteIcon={<PersonRemoveIcon />}
                            title={member.nickname ? `${member.name} (${member.email})` : member.email}
                          />
                        );
                      })
                    ) : (
                      <Typography variant="caption" color="text.secondary">
                        No members yet
                      </Typography>
                    )}
                  </Box>
                </Box>
              }
            />
            <ListItemSecondaryAction>
              <IconButton
                edge="end"
                aria-label="add member"
                onClick={() => handleAddMemberClick(group)}
                sx={{ mr: 1 }}
              >
                <PersonAddIcon />
              </IconButton>
              <IconButton
                edge="end"
                aria-label="delete"
                onClick={() => handleDeleteClick(group)}
              >
                <DeleteIcon />
              </IconButton>
            </ListItemSecondaryAction>
          </ListItem>
        ))}
      </List>

      <Dialog open={deleteDialogOpen} onClose={() => setDeleteDialogOpen(false)}>
        <DialogTitle>Delete Group</DialogTitle>
        <DialogContent>
          Are you sure you want to delete this group?
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDeleteDialogOpen(false)}>Cancel</Button>
          <Button onClick={handleDelete} color="error">Delete</Button>
        </DialogActions>
      </Dialog>

      <Dialog 
        open={addMemberDialogOpen} 
        onClose={() => {
          setAddMemberDialogOpen(false);
          setNewMemberEmail('');
          setNewMemberNickname('');
        }}
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle>Add Member</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label="Email Address"
            type="email"
            fullWidth
            value={newMemberEmail}
            onChange={(e) => setNewMemberEmail(e.target.value)}
            required
            sx={{ mb: 2, mt: 2 }}
          />
          <TextField
            margin="dense"
            label="Nickname (Optional)"
            fullWidth
            value={newMemberNickname}
            onChange={(e) => setNewMemberNickname(e.target.value)}
            placeholder="Enter a nickname for this member"
            helperText="Leave empty to use their name"
          />
        </DialogContent>
        <DialogActions>
          <Button 
            onClick={() => {
              setAddMemberDialogOpen(false);
              setNewMemberEmail('');
              setNewMemberNickname('');
            }}
          >
            Cancel
          </Button>
          <Button 
            onClick={handleAddMember} 
            color="primary"
            disabled={!newMemberEmail.trim()}
          >
            Add
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default NoteGroupList; 